<template>
  <div></div>
</template>
<script>
import { ticketLogin } from '@/API/user'

export default {
  name: 'vendor',
  created () {
    const { token } = this.$route.query
    ticketLogin({ token }).then(res => {
      this.$store.commit('setXbbBonus', { ...res.rescont.vendor, isGuest: res.rescont.is_guest })
      this.$router.replace({ name: 'home' })
    }, () => {
      this.$store.commit('setXbbBonus', { isGuest: true })
      this.$router.replace({ name: 'home' })
    })
  }
}
</script>



